.hero_bg {
  padding: 20% 0% 12.5% 0%;
  border-bottom: 1px solid #f9bb00;
  height: 890px;
  background-color: rgba(0, 0, 0, 0.5);
}

.videoBg {
  position: absolute;
  z-index: -1;
  top: 0px;
  left: 0px;
  height: 890px;
  width: 98.9vw;
  opacity: 1;
  overflow: hidden;
}

.videoBg video {
  position: absolute;
  z-index: -1;
  top: 0px;
  left: 0px;
  min-width: 100%;
  min-height: 100%;
}

.hero {
  width: 50%;
  margin: 0% auto 0px auto;
}

.hero_img {
  width: 190px;
  max-width: inherit !important;
}

.hero_logo {
  width: 25%;
  vertical-align: top;
}

.hero_title {
  width: 75%;
  padding: 0px 0px 0px 10px;
  vertical-align: top;
}

.hero_h1 {
  font-size: 113px;
  font-weight: bold;
  margin: 0px;
}

.hero_h2 {
  font-size: 28px;
  margin: 0px;
  text-align: center;
}

.hero_h3 {
  font-size: 28px;
  margin: 0px;
  padding-left: 5px;
  text-align: center;
}

/* Small devices (landscape phones, 576px and up) */
@media (max-width: 576px) {
  .hero_bg {
    height: 300px;
  }

  .videoBg {
    height: 300px;
  }
}

